import React from "react"
import "../style.css"



function Projectlinks(props){
  return(

    <a href={props.projectlink} target="_blank" rel="noopener noreferrer"
    className="view-first">
    <div className="view">


    <img src={props.projectimg} className="imgsize" alt="Project" />

    <h3 className="titleproject">{props.titleofproject}</h3>


    </div>

    </a>
  )
}




export default Projectlinks
